.Component__Wrap{
    display: flex;
    justify-content: center;
	width: 100%;
}
.test{
    width:100%;
    height:100vh;
}

.to-top{
	color:white;
	position: fixed;
	bottom: 0;
	left: 50%;
    transform: translate( -50%, -50% );
    text-decoration: none;
}
.to-top span {
    position: absolute;
    left: 50%;
    width: 46px;
    height: 46px;
    margin-left: -23px;
    border: 1px solid #fff;
    border-radius: 100%;
    box-sizing: border-box;
    bottom:0;
}
.to-top span::after {
    position: absolute;
    top: 50%;
    left: 50%;
    content: '';
    width: 16px;
    height: 16px;
    margin: -12px 0 0 -8px;
    border-left: 1px solid #fff;
    border-bottom: 1px solid #fff;
    -webkit-transform: rotate(-45deg);
    transform: rotate(-45deg);
    box-sizing: border-box;
}